<script lang="ts" setup>

import {useGameStore} from "@/stores/game-store";
import ClientActions from "@/consts/client-actions";
import {computed, onBeforeUnmount, onMounted, ref} from "vue";
import VSpinePlayer from "@/components/Utility/SpinePlayer/VSpinePlayer.vue";
import {useDebounce} from "@/composible/debounce";
import {useThrottler} from "@/composible/throttler";
import ServerActions from "@/consts/server-actions";
import Random from "@/modules/Random";
import VTap from "@/components/Game/VTap.vue";
import {useClickHints} from "@/composible/click-hints";
import {useTimeMachine} from "@/composible/time-machine";

interface DestroyElement{
	n : number,
	v : number
}

interface ExplosionElement{
	x : number,
	y : number,
	id : number,
}

const gameStore = useGameStore();
const clickHints = useClickHints();
const timeMachine = useTimeMachine();
const emit = defineEmits(['loaded']);

let destroyElements = ref<DestroyElement[]>([]),
	explosionElements   = ref<ExplosionElement[]>([]);

let canTouch = computed<boolean>(() => gameStore.user!.energy >= gameStore.game!.energy.spendRate);

let destroyThrottler = useThrottler(1000),
	clearDebounce = useDebounce(2000),
	explosionSpawnThrottler = useThrottler(1000);

function onGameClick(event : MouseEvent){

	if(!canTouch){
		return;
	}

	gameStore.socket.emit(ClientActions.TAP);

	destroyThrottler(() => {
		destroyElements.value.push({
			n : Random.int(1, 4),
			v : Random.int(1, 7)
		});
	});

	clearDebounce(() => {
		destroyElements.value = [];
	});

	clickHints.push(event.clientX - 10, event.clientY - 10, '+' + gameStore.game!.energy.rewardRate, 40);

}

function onJailClick(e : MouseEvent){

	if(!canTouch){
		return;
	}

	explosionSpawnThrottler(() => {

		let explosion = {
			x : e.clientX,
			y : e.clientY,
			id : Date.now()
		};

		explosionElements.value.push(explosion);

		timeMachine.timeout(() => {
			explosionElements.value.splice(
				explosionElements.value.indexOf(explosion), 1
			);
		}, 700);

	});

}

function onServerAnswerTap(){


}

onMounted(() => {

	gameStore.socket.on(ServerActions.ON_TAP, onServerAnswerTap);

	emit('loaded');

});

onBeforeUnmount(() => {
	gameStore.socket.off(ServerActions.ON_TAP, onServerAnswerTap);
});

</script>
<template>
	<main class="game" @click.prevent="onGameClick">
		<div class="game__rain"></div>
		<VSpinePlayer
			atlas="/assets/animations/bg/work_02.atlas"
			skeleton="/assets/animations/bg/work_02.json"
			animation="animation"
			loop
			class="game__background" />
		<VSpinePlayer
			atlas="/assets/animations/durov/work_02.atlas"
			skeleton="/assets/animations/durov/work_02.json"
			animation="animation"
			loop
			class="game__person" />
		<div class="game__jail jail">
			<div class="jail__item" v-for="k in 4" :key="k" @click.prevent="onJailClick">
				<TransitionGroup name="fade">
					<div
						v-for="element in destroyElements.filter(element => element.n === k)"
						:key="`${element.n}_${element.v}`"
						class="jail__item-damaged"
						:class="`jail__item-damaged--view-${element.v}`"
					></div>
				</TransitionGroup>
			</div>
		</div>
		<div class="game__taps">
			<div class="game__taps-item tap"
			     v-for="tap in explosionElements"
			     :key="tap.id"
			     :style="{ top : tap.y + 'px', left : tap.x + 'px' }">
			</div>
		</div>
	</main>
</template>
