import {defineStore} from "pinia";
import {ref} from "vue";

interface ClickHintsItem{
	text : string,
	size : number,
	x : number,
	y : number,
	id : string
}

export const useClickHintsStore = defineStore('click-hints-store', () => {

	let list = ref<ClickHintsItem[]>([]),
		increment = ref<number>(0);

	return {
		increment,
		list
	}


});